.header {
    display: flex;
    justify-content: space-between; /* 왼쪽과 오른쪽 정렬 */
    align-items: center;
    padding: 20px 50px;
    background-color: #141414;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    box-sizing: border-box; /* 패딩 포함하여 크기 계산 */
  }
  
  .header__nav {
    display: flex;
    flex: 1; /* 네비게이션 바가 전체 너비 차지 */
    justify-content: space-around; /* 버튼들 사이 간격 균등 */
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .header__nav li {
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    color: white;
    transition: color 0.3s ease;
  }
  
  .header__nav li:hover {
    color: #e50914; /* Netflix 빨간색으로 호버 시 강조 */
  }
  
  .header__logo {
    height: 35px;
    cursor: pointer;
  }
  
  .header__profile {
    display: flex;
    align-items: center;
    justify-content: center; /* 수평 중앙 정렬 */
    position: relative; /* 필요시 위치 조정 */
  }
  
  .header__avatar {
    width: 40px; /* 이미지 크기 조정 */
    height: 40px;
    border-radius: 50%; /* 둥근 이미지 */
    cursor: pointer;
    object-fit: cover; /* 이미지 왜곡 방지 */
    z-index: 1; /* 다른 요소 위로 배치 */
  }
  
  /* 문제 발생 가능성 해결을 위해 추가 */
  .header__profile img {
    display: block; /* 이미지가 inline으로 간주되지 않도록 설정 */
  }
  
  